<template>
  <AppCard
    v-if="data"
    basis="500px"
    :title="'Информация о заказе #' + data.id"
  >
    <FormKit
      v-model="data"
      type="form"
      :actions="false"
    >
      <FormKitSchema :schema="orderSchema" />
    </FormKit>
  </AppCard>
</template>

<script setup lang="ts">
import AppCard from '@/components/markup/AppCard.vue';

const props = defineProps<{
  data: OrderFormData;
}>();
const data = ref(null as null | OrderFormData);
const orderSchema = [
  {
    $formkit: 'primeCheckbox',
    name: 'vatDocumentRequired',
    label: 'Требуется документ для VAT',
    _disabled: true,
  },
  {
    $formkit: 'primeInputText',
    name: 'shippingMethodName',
    label: 'Способ получения',
    validation: 'required',
    _disabled: true,
  },
  {
    $formkit: 'primeTextarea',
    name: 'comment',
    label: 'Коментарий',
    rows: '5',
    _disabled: true,
  },
];

onUpdated(() => {
  data.value = Object.assign({}, props.data);
});
</script>

<script lang="ts">
export interface OrderFormData {
  id: number;
  vatDocumentRequired: boolean;
  shippingMethodName: number;
  comment: string;
}
</script>
